import { Button, Cell, Dialog } from "@taroify/core"
import { ArrowRight } from "@taroify/icons"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicDialog() {
  return (
    <>
      <Dialog id="dialog" />
      <Cell
        title=""
        clickable
        bordered
        rightIcon={<ArrowRight />}
        onClick={() => Dialog.alert("")}
      />
    </>
  )
}

function NoTitleDialog() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        title=""
        clickable
        bordered
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <Dialog open={open} onClose={setOpen}>
        <Dialog.Content></Dialog.Content>
        <Dialog.Actions>
          <Button onClick={() => setOpen(false)}></Button>
        </Dialog.Actions>
      </Dialog>
    </>
  )
}

function ConfirmDialog() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        title=""
        clickable
        bordered
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <Dialog open={open} onClose={setOpen}>
        <Dialog.Header></Dialog.Header>
        <Dialog.Content></Dialog.Content>
        <Dialog.Actions>
          <Button onClick={() => setOpen(false)}></Button>
          <Button onClick={() => setOpen(false)}></Button>
        </Dialog.Actions>
      </Dialog>
    </>
  )
}

function RoundDialog() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        title=""
        clickable
        bordered
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <Dialog open={open} onClose={setOpen}>
        <Dialog.Header></Dialog.Header>
        <Dialog.Content></Dialog.Content>
        <Dialog.Actions theme="round">
          <Button onClick={() => setOpen(false)}></Button>
          <Button onClick={() => setOpen(false)}></Button>
        </Dialog.Actions>
      </Dialog>
    </>
  )
}

function RoundNoTitleDialog() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        title=""
        clickable
        bordered
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <Dialog open={open} onClose={setOpen}>
        <Dialog.Content></Dialog.Content>
        <Dialog.Actions theme="round">
          <Button onClick={() => setOpen(false)}></Button>
        </Dialog.Actions>
      </Dialog>
    </>
  )
}

export default function DialogDemo() {
  return (
    <Page title="Dialog " className="dialog-demo">
      <Block variant="card" title="">
        <BasicDialog />
        <NoTitleDialog />
        <ConfirmDialog />
      </Block>
      <Block variant="card" title="">
        <RoundDialog />
        <RoundNoTitleDialog />
      </Block>
    </Page>
  )
}
